.student-exam-room {
    margin-top: 5rem;
}
.ser-container {
    border-left: 1px solid #d1d1d1;
    border-right: 1px solid #d1d1d1;
}
.ser-header {
    z-index: 555;
    width: 100%;
    height: 3.5rem;
    background-color: #2c4158;
    display: flex;
    justify-content: space-around;
    align-items: center;
    position: fixed;
}
.ser-header-text {
    color: #fafafa;
    padding-top: 3px;
    text-align: center;
    font-size: 1.8rem;
    font-weight: 600;
}
.ser-single {
    padding: 10px;
    display: flex;
    flex-basis: 100%;
    justify-content: space-between;
    border-bottom: 1px solid #d1d1d1;
}
.ser-serial {
    flex-basis: 3%;
    text-align: center;
}
.ser-data {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.ser-data-title {
    flex-grow: 1;
    margin-bottom: 1rem;
}
.ser-data-options {
    /* flex-grow: 1; */
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
}

.ser-data-option {
    display: flex;
    justify-content: flex-start;
}
.ser-data-option-no {
    margin-right: 10px;
    flex-basis: 3%;
    text-align: center;
}
.ser-data-option-text {
}

.ser-mark {
    flex-basis: 3%;
    text-align: center;
}

.ser-container h3 {
    font-size: 1.6rem;
    font-weight: 700;
}
.ser-data-options h3 {
    font-size: 1.6rem;
    font-weight: 500;
}

.ans {
    width: 100%;
    margin-left: 1rem;
    font-family: inherit;
    font-size: 1.6rem;
}
.ser-opt-text {
    margin: 3px 10px;
    padding: 0;
}
.ser-button {
    font-family: inherit;
    padding: .5rem 1rem;
    margin: 0 .5rem;
    border-radius: 2px;
    border: none;
    background-color: #f7f7f7;
    
}
.ser-button:hover {
    background-color: green;
    color: #f7f7f7;
}
.ser-body {
    margin: 0rem 15rem;
    padding-top: 3.5rem;
}

.ser-data-image img{
    width: 100px;
    height: 100px;
}
.ser-data-contain {
    display: flex;
    justify-content: space-between;
}
