.exam-room {
  display: flex;
  margin-top: 5rem;
  height: 92.3vh;
  position: relative;
}
.er-left {
  flex-basis: 22%;
  border-right: 1px solid #d1d1d1;
  display: flex;
  flex-direction: column;
}
.er-middle {
  flex-basis: 53%;
}

.er-right {
  flex-basis: 25%;
  border-left: 1px solid #d1d1d1;
  display: flex;
  flex-direction: column;
}
.er-teacher {
  height: 18%;
}
.er-students {
  height: 82%;
}
.er-exam-details {
  height: 72%;
}
.er-my-profile {
  height: 20%;
}
.er-countdown {
}

.er-header {
  width: 100%;
  height: 3.5rem;
  background-color: #2c4158;
}
.er-header-text {
  color: #fafafa;
  padding-top: 3px;
  text-align: center;
  font-size: 1.8rem;
  font-weight: 600;
}
.er-data {
  padding-left: 3%;
  padding-top: 5px;
  height: 3rem;
  font-size: 1.6rem;
  border-bottom: 1px solid #d1d1d1;
  background-color: #fafafa;
}
.er-data-student {
  padding-left: 3%;
  height: 3rem;
  font-size: 1.6rem;
  border-bottom: 1px solid #d1d1d1;
  background-color: #fafafa;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.er-status {
  margin-right: 15px;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background-color: #fafafa;
  border: 1px solid #a1a1a1;
}
.er-green {
  background-color: green;
  border: none;
}
.er-yellow {
  background-color: yellow;
  border: 1px solid #a1a1a1;
}
.er-red {
  background-color: red;
  border: none;
}
.er-controls {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 1rem;
}

.er-button {
  margin: 1rem auto;
  font-size: 1.6rem;
  padding: .5rem 3rem;
  border: none;
  border-radius: 3px;
  background-color: #2c4158;
  color: #f7f7f7;
}
.er-button:hover {
  background-color: #3a6491;
  border-radius: 3px;
  outline: none;
  border: none;
}

.er-columns {
  display: flex;
  justify-content: space-between;
}
.er-column {
  padding: 5px;
}
.er-confirmation {
  position: absolute;
  z-index: 666;
  width: 60rem;
  height: 40rem;
  background-color: #f7f7f7;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.er-confirmation-container {
  height: 80%;
  width: 85%;
  margin: 5.5% auto;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.er-confirmation-header {
  flex-basis: 15%;
  font-family: inherit;
  font-weight: 700;
  color: green;
  font-size: 2rem;
  text-align: center;
  border-bottom: 1px solid #b1b1b1;
}
.er-confirmation-body {
  border-bottom: 1px solid #b1b1b1;
  flex-grow: 1;
}
.er-confirmation-info {
  font-size: 1.6rem;
}
.er-confirmation-footer {
  flex-basis: 15%;
}
.er-confirmation-ans {
  width: 90%;
  height: 2rem;
  margin: 1rem;
  font-family: inherit;
  font-size: 1.6rem;
}
.ecc-text {
  margin: 0px 0 0 5px;
  padding: 0;
  width: 100%;
  font-size: 1.4rem;
  color: #0b2d4d;
}