.sm-switch {
    position: relative;
    display: inline-block;
    width: 3.5rem;
    height: 1.9rem;
    margin-top: 3px;
  }
  
  .sm-switch input { 
    opacity: 0;
    width: 0;
    height: 0;
  }
  
  .sm-slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #ccc;
    -webkit-transition: .4s;
    transition: .4s;
  }
  
  .sm-slider:before {
    position: absolute;
    content: "";
    height: 15px;
    width: 15px;
    left: 2px;
    bottom: 2px;
    background-color: white;
    -webkit-transition: .4s;
    transition: .4s;
  }
  
  input:checked + .sm-slider {
    background-color: #2196F3;
  }
  
  input:focus + .sm-slider {
    box-shadow: 0 0 1px #2196F3;
  }
  
  input:checked + .sm-slider:before {
    -webkit-transform: translateX(15px);
    -ms-transform: translateX(15px);
    transform: translateX(15px);
  }
  
  .sm-slider.round {
    border-radius: 50px;
  }
  
  .sm-slider.round:before {
    border-radius: 50%;
  }