.result-container {

}
.result-container h1 {
    text-align: center;
}
.result-container h2 {
    text-align: center;
}
.result-table td {
    padding: 1rem;
}
.r-heading {
    width: 100%;
    display: flex;
    justify-content: center;
}
.r-left {
    flex-basis: 25%;
    align-self: flex-end;
    margin-left: 1rem;
}
.r-middle {
    flex-basis: 50%;
}
.r-right {
    flex-basis: 25%;
    
}
.s-print {
    width: 3rem;
}
.student_result {
    width: 80rem;
    padding: 2rem 2rem;
    position: fixed;
    top: 16%;
    left: 22%;
    z-index: 999;
    background-color: #fff;
}
.student_result_heading {
    border: 1px solid blue;
    
}
.student_result_heading h2 {
    text-align: center;
}

.student_result_info {
    border: 1px solid red;
}

.student_result_body {
    border: 1px solid red;
    height: 40rem;
}




.std-ans-container {
    margin: 0px 5px;
    height: 92vh;
    border-left: 1px solid #d1d1d1;
    border-right: 1px solid #d1d1d1;
}
.std-ans-header {
    height: 3.5rem;
    background-color: #2c4158;
}
.std-ans-header-text {
    color: #fafafa;
    padding-top: 3px;
    text-align: center;
    font-size: 1.8rem;
    font-weight: 600;
}
.std-ans-single {
    padding: 10px;
    display: flex;
    flex-basis: 100%;
    justify-content: space-between;
    border-bottom: 1px solid #d1d1d1;
}
.std-ans-serial {
    flex-basis: 3%;
    text-align: center;
}
.std-ans-data {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.std-ans-data-title {
    flex-grow: 1;
}
.std-ans-data-options {
    /* flex-grow: 1; */
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
}

.std-ans-data-option {
    display: flex;
    justify-content: flex-start;
}
.std-ans-data-option-no {
    margin-right: 10px;
    flex-basis: 3%;
    text-align: center;
}
.std-ans-data-option-text {
}

.std-ans-mark {
    flex-basis: 3%;
    text-align: center;
}

.std-ans-container h3 {
    font-size: 1.6rem;
    font-weight: 700;
}
.std-ans-data-options h3 {
    font-size: 1.6rem;
    font-weight: 500;
}
.std-ans-data-box {
    display: flex;
    justify-content: space-between;
}
.std-ans-data-image img {
    width: 100px;
    height: 100px;
}