.qp-container {
    margin: 0px 5px;
    height: 92vh;
    border-left: 1px solid #d1d1d1;
    border-right: 1px solid #d1d1d1;
}
.qp-header {
    height: 3.5rem;
    background-color: #2c4158;
}
.qp-header-text {
    color: #fafafa;
    padding-top: 3px;
    text-align: center;
    font-size: 1.8rem;
    font-weight: 600;
}
.qp-single {
    padding: 10px;
    display: flex;
    flex-basis: 100%;
    justify-content: space-between;
    border-bottom: 1px solid #d1d1d1;
}
.qp-serial {
    flex-basis: 3%;
    text-align: center;
}
.qp-data {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.qp-data-title {
    flex-grow: 1;
    margin-bottom: 1rem;
}
.qp-data-options {
    /* flex-grow: 1; */
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
}

.qp-data-option {
    display: flex;
    justify-content: flex-start;
}
.qp-data-option-no {
    margin-right: 10px;
    flex-basis: 3%;
    text-align: center;
}
.qp-data-option-text {
}

.qp-mark {
    flex-basis: 3%;
    text-align: center;
}

.qp-container h3 {
    font-size: 1.6rem;
    font-weight: 700;
}
.qp-data-options h3 {
    font-size: 1.6rem;
    font-weight: 500;
}
.qp-data-box {
    display: flex;
    justify-content: space-between;
}
.qp-data-image img {
    width: 100px;
    height: 100px;
}